
import { mapMutations } from "vuex"

export default {
		
	methods:{
		...mapMutations(['login'])
	},
	onLaunch: function() {
		console.log('App Launch')
		let userInfo = uni.getStorageSync('userInfo') || '';
		if(userInfo.id) {
			// 更新登录状态
			uni.setStorage({
				key:'userInfo',
				success: (res) => {
					this.login(userInfo);
				}
			})
		}
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
